.header {
  position: relative;
  // overflow: hidden
  &.is-logined {
    .links__item_logout {
      display: block; } }
  &__inner {
    @include clr; }
  &__logo {
    float: left;
    padding: 12px 0;
    @include r(1200) {
      float: none;
      text-align: center; }
    @include r(960) {
      padding-bottom: 0; } }
  &__info {
    margin-top: 10px;
    float: right;
    @include r(1200) {
      float: none;
      text-align: center; }
    @include md {
      margin-bottom: 10px; } }
  &__country {
    padding: 10px 20px;
    float: left;
    @include r(1200) {
      padding-left: 0;
      float: none; } }
  &__middle {
    border-top: 1px solid transparent;
    @include r(960) {
      border-top-color: $yellow2; } }
  &__locator {
    float: right;
    @include r(1200) {
      display: block;
      float: none; } }

  &__bottom {
    .inner {}
    padding: 0 15px;
    @include r(1200) {
      padding: 0 10px; } }

  &__logo-sm {
    display: inline-block;
    vertical-align: middle;
    padding: 5px 0 5px 20px;
    @include r(1200) {
      padding-left: 0; }
    @include r(960) {
      display: none; } }
  &__top-btn {
    display: none;
    @include r(960) {
      display: inline-block;
      vertical-align: middle;
      float: left;
      margin-right: -60px;
      button {
        position: relative;
        // z-index: 99999999
        display: inline-block;
        vertical-align: middle;

        &:not(:last-child) {
          margin-right: 10px; } } } }



