.inner {
  max-width: 1220px;
  width: 100%;
  margin: 0 auto;
  padding: 0 10px;
  @include r(960) {
    padding: 0 12px; }
  // display: flex
  // flex-flow: row nowrap
 }  // justify-content: space-between

.d-sm-block {
    display: none;
    @include sm {
        display: block; } }

.d-md-block {
    display: none;
    @include md {
        display: block; } }

@include lg {
    .d-lg-none {
        display: none; } }

@include md {
    .d-md-none {
        display: none; } }

@include sm {
    .d-sm-none {
        display: none; } }


